<template>
  <button
    type="button"
    class="flat-btn"
    @click="onClick"
    @touchstart="onTouchStart"
  >
    <slot></slot>
  </button>
</template>

<script>
export default {
  name: "Button",
  methods: {
    onClick: function() {
      this.$emit("click");
    },
    onTouchStart: function(event) {
      const button = event.currentTarget;
      button.classList.add("active");
      setTimeout(function() {
        button.classList.remove("active");
      }, 300);
    }
  }
};
</script>
